.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: rgb(10, 120, 153);

  &__container {
    margin: 0 auto;
    max-width: 1020px;
    padding: 0 20px;

    @include onDesctop {
      padding: 0 40px;
    }
  }

  &__content {
    padding: 20px 0;
  }

  &__top {
    margin-bottom: 48px;
  }

  &__list {
    margin: 0 0 48px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding: 8px;
    border-bottom: 1px solid transparent;
    font-weight: bold;
    font-size: 22px;
    line-height: 22px;
    color: #fff;
    text-decoration: none;
    letter-spacing: 2px;
    text-transform: uppercase;
    transition: border-color 0.3s;
    &:hover {
      border-color: #fff;
    }
  }

  &__call {
    display: block;
    width: max-content;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #fff;
    text-decoration: none;

    &--phone {
      margin-bottom: 16px;
    }

    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid #fff;
    }
  }
}
